import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import * as courses from "./../shared/courses";

@Component({
  selector: "app-course-detail",
  templateUrl: "./course-detail.component.html",
  styleUrls: ["./course-detail.component.css"]
})
export class CourseDetailComponent implements OnInit {
  public id: string;
  public course_items = courses.courses;

  course: any;

  constructor(private route: ActivatedRoute) {
    this.id = this.route.snapshot.paramMap.get("id");
    console.log(this.id);
    /*
    **
      var test = this.getDimensionsByFilter(this.id);
      console.log(test);
    **
    */

    this.course = this.getDimensionsByFind(this.id);
    console.log(this.course);
  }

  ngOnInit() {
    /*
    **
      this.id = this.route.snapshot.paramMap.get("id");
      console.log(this.id);
      var test = this.getDimensionsByFilter(this.id);
      console.log(test);
      var course = this.getDimensionsByFind(this.id);
      console.log(course);
    **
    */

  }

  getDimensionsByFilter(id) {
    return this.course_items.filter(x => x.id === id);
  }

  getDimensionsByFind(id) {
    return this.course_items.find(x => x.id === id);
  }
}
